import { ModalModule } from 'ng2-bootstrap/modal';
import { StorageService } from './../../../../services/storage.service';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';


import { PopupModalModule } from 'app/shared/popup/popup-modal.module';
import { CategoriaService } from './categoria.service';
import { SharedComponentModule } from './../../../shared/shared-component.module';
import { CategoriaRoutingModule } from './categoria-routing.module';

import { ListarCategoriaComponent } from './listar/listar-categoria.component';
import { CategoriaPopupModalComponent } from './popup/categoria-popup-modal.component';



@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule.forRoot(),
    SharedComponentModule,
    ReactiveFormsModule,
    PopupModalModule,
    HttpClientModule,
    CategoriaRoutingModule
  ],
  declarations: [
    ListarCategoriaComponent,
    CategoriaPopupModalComponent
  ],
  providers:[
    CategoriaService,
    StorageService
  ]
})
export class CategoriaModule { }



