import { CategoriaDTO } from 'models/domain/categoria.dto';
import { CategoriaService } from './../categoria.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';

import { AbstractPopupComponent } from 'config/core/abstract-popup.component';
import { ImageService } from 'services/image.service';



@Component({
  selector: 'categoria-popup-modal',
  templateUrl: './categoria-popup-modal.component.html',
  styleUrls: ['./categoria-popup-modal.component.scss']
})
export class CategoriaPopupModalComponent extends AbstractPopupComponent implements OnInit {

  checkExibir: boolean = false;
  desabilitarCampos: boolean = false;

  categoria: CategoriaDTO;
  constructor(
    public categoriaService: CategoriaService,
    public image: ImageService,
    public fb: FormBuilder) {
    super("categoriasPoup");

  }

  ngOnInit() {
    this.criarForm();
  }

  show() {
    this.criarForm();
    this.showPopup("Cadastra categoria");
  }

  showAlteracao(item: CategoriaDTO) {
    this.alterarForm(item)
    this.showPopup("Alterar categoria");
  }

  btnConfirmar(categoria: CategoriaDTO) {
    categoria.urlImagem = this.urlFoto;
    this.acao(categoria, this.categoriaService);
  }

  mudarImage(evento) {
    this.atualizarFoto(evento.foto);
  }

  public alterarCheckExibir(valor: boolean) {
    this.formulario.patchValue({
      exibirConsulta: valor ? 'S' : 'N'
    });
  }

  private alterarForm(categoria: CategoriaDTO) {
    this.image.getImageFromBucket(categoria.id, 'CAT')
      .subscribe(
        res =>  this.urlFoto = this.image.getImage(categoria.id, 'CAT') ,
        error =>  this.urlFoto = this.urlFotoAvatar 
      )
    this.formulario.patchValue({
      id: categoria.id,
      nmCategoria: categoria.nmCategoria,
      exibirConsulta: categoria.exibirConsulta,
      status: categoria.status,
      dsComentario: categoria.dsComentario,
      urlImagem: categoria.urlImagem,
      emAlteracao: true
    });
    this.desabilitarCampos = categoria.id =='1';
    this.checkExibir = categoria.exibirConsulta == 'S';
    this.urlFoto = categoria.urlImagem;
  }

  private criarForm() {
    this.urlFoto = this.urlFotoAvatar;
    this.formulario = this.fb.group({
      id: [],
      nmCategoria: ['', Validators.required],
      dsComentario: [''],
      exibirConsulta: ['S'],
      status: ['A'],
      foto: [''],
      emAlteracao: [false]
    })
    this.desabilitarCampos = false;
  }

  private atualizarFoto(foto: any) {
    this.urlFoto = foto;
  }


}



