import { TrataErrorService } from './../../../../../services/trata-error.service';
import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { Component, OnInit, ViewChild } from '@angular/core';
import { API_CONFIG } from 'config/core/api.config';
import { CategoriaService } from '../categoria.service';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { Router } from '@angular/router';

import { CategoriaDTO } from 'models/domain/categoria.dto';


@Component({
  templateUrl: './listar-categoria.component.html',
  styleUrls: ['./listar-categoria.component.scss']
})
export class ListarCategoriaComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('excluirModal') popupExclusao;
  @ViewChild('changeSatusModal') popupChangeStatus;

  item: CategoriaDTO;
  items: CategoriaDTO[];

  constructor(
    public categoriaService: CategoriaService,
    public router: Router) {
    super();
  }

  ngOnInit() {
    this.carregarTodos();
  }

  selecionarItem(item: CategoriaDTO) {
    this.item = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: CategoriaDTO) {
    this.item = item;
    this.popupChangeStatus.show();
  }

  changeStatus() {
    this.categoriaService.changeStatus(this.item)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Categorias', 'Status da categoria alterado com sucesso!!!')
        this.carregarTodos();
      }, error => TrataErrorService.tratarError(error));
  }

  excluir() {
    this.categoriaService.excluir(this.item)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Categorias', 'Categoria excluída com sucesso!!!')
        this.carregarTodos();
      }, error => TrataErrorService.tratarError(error));
  }

  public carregarTodos() {
    this.categoriaService.findAll().subscribe(response => this.items = response, error => { });
  }

}



